import React from "react";
import {
  Table,
  Thead,
  Tbody,
  Tfoot,
  Tr,
  Th,
  Td,
  TableCaption,
  TableContainer,
} from "@chakra-ui/react";
import Item from "./Item";

function PartPickerTable() {
  return (
    <TableContainer pt={["75", "100"]} pl="10%" pr="10%">
      <Table variant="simple" boxShadow="lg">
        <Thead bg="teal.400">
          <Tr>
            <Th>Компонент</Th>
            <Th>Название</Th>
            <Th isNumeric>Цена</Th>
          </Tr>
        </Thead>
        <Tbody>
        </Tbody>
      </Table>
    </TableContainer>
  );
}

export default PartPickerTable;
