import React from "react";
import {
  Table,
  Thead,
  Tbody,
  Tfoot,
  Tr,
  Th,
  Td,
  TableCaption,
  TableContainer,
  useBoolean,
} from "@chakra-ui/react";
import { Button } from "@chakra-ui/react";
import PartPicker from "./PartPicker";

function Item({ part, name, price }) {
  const [flag, setFlag] = useBoolean();
  return (
    <Tr>
      <Td>{part}</Td>
      <Td>
        {name}
        <Button colorScheme={"teal"} onClick={setFlag.on}>
          + Добавить
        </Button>
        {flag && <PartPicker flag={flag} close={setFlag}></PartPicker>}
      </Td>
      <Td isNumeric>{price}</Td>
    </Tr>
  );
}

export default Item;
