import React from "react";
import {
  Table,
  Thead,
  Tbody,
  Tfoot,
  Tr,
  Th,
  Td,
  TableCaption,
  TableContainer,
} from "@chakra-ui/react";
import Item from "./Item";

function ItemTable() {
  return (
    <TableContainer pt={["75", "100"]} pl="10%" pr="10%">
      <Table variant="simple" boxShadow="lg">
        <TableCaption>Гавно это ваше проганье фронта</TableCaption>
        <Thead bg="teal.400">
          <Tr>
            <Th>Компонент</Th>
            <Th>Название</Th>
            <Th isNumeric>Цена</Th>
          </Tr>
        </Thead>
        <Tbody>
          <Item part="Процессор" name="" price=""></Item>
          <Item part="Материнская плата" name="" price=""></Item>
          <Item part="Корпус" name="" price=""></Item>
          <Item part="Видеокарта" name="" price=""></Item>
          <Item part="Оперативная память" name="" price=""></Item>
          <Item part="Блок питания" name="" price=""></Item>
          <Item part="Охлаждение процессора" name="" price=""></Item>
        </Tbody>
      </Table>
    </TableContainer>
  );
}

export default ItemTable;
