import React from "react";
import { Box, Flex, Image, Heading, Text, Button } from "@chakra-ui/react";
import InfoDrawer from "./InfoDrawer";

function Header({ text }) {
  return (
    <Flex bg="teal.600" justify="space-between" position="fixed" w="100%" zIndex="999">
      <Flex align="center">
        <Image
          w={["25%", "25%"]}
          p={[2, 3]}
          src="https://cdn.icon-icons.com/icons2/196/PNG/128/computer_23805.png"
          href="https://cdn.icon-icons.com/icons2/"
        ></Image>
        <Heading size={["sm", "md"]} color="white">
          ПКСборкер
        </Heading>
      </Flex>
      <Flex align="center" p={[2, 3]}>
        <InfoDrawer />
      </Flex>
    </Flex>
  );
}

export default Header;
