import React from "react";
import { Box } from "@chakra-ui/react";
import {
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AccordionIcon,
} from "@chakra-ui/react";
import ItemTable from "./ItemTable";

function Contents() {
  return (
    <>
      <ItemTable></ItemTable>
      <Accordion pl="10%" pr="10%" allowToggle>
        <AccordionItem>
          <h2>
            <AccordionButton bg="teal.400" _hover={{ bg: "teal.400" }}>
              <Box flex="1" textAlign="left">
                Дополнительные комплектующие
              </Box>
              <AccordionIcon />
            </AccordionButton>
          </h2>
          <AccordionPanel pb={4}>
            <ItemTable></ItemTable>
          </AccordionPanel>
        </AccordionItem>
      </Accordion>
    </>
  );
}

export default Contents;
